import React, {Component} from 'react';
import { Link } from 'react-router-dom';
import Axios from "axios";

const SERVER_BASE_URL = require("../params").serverBaseUrl;

export default class LoginForm extends Component {

	state = {
		backendHasStarted: false
	}
	componentDidMount() {
		/* To force Backend Server Startup (Heroku !) */
		Axios.get(SERVER_BASE_URL+'/projects')
		.catch(() => console.log("Server woke up !")) /* Won't work b/C you need JWT */
		.then(() => this.setState({ backendHasStarted: true}))
	}

	render() {
			return (
				<div className="container mt-5">
					<div className="row">
						<div className="col-6 mt-5">
							<h2>Online Tester</h2>
							<p>
								A Web application I made in my summer internship at MAP.ma for Test Management.
								I used React.js for Front End and Spring Boot in Back end.<br/>
								It requires JWT authentication in order to access the data from the server. <br/>
								The full internship report will be available in French in my Github Repo.<br/>
								If you need more clarifications or want to report a bug you can contact me at :  <a href="mailto:youneskasri@gmail.com">youneskasri@gmail.com</a>						
							</p>
							<p>			
								<span className="text-warning font-weight-bold">Disclaimer ! </span> 
								I used Heroku free trial to host the backend, that means that the server sleeps after 30min of inactivity,
								as a result the <span className="font-weight-bold">first request can take a few seconds</span> for the server startup.
							</p>
							<div>	
								<span className="text-danger font-weight-bold">Disclaimer ! </span>
								For this demonstration, I used an in-memory database. All the modifications you add will be deleted after 30min of inactivity.
							</div>
						</div>
						<div className="col-6 mt-5">
							<h2>How To Use ?</h2>
							<div>
								Wait until the "Start now" button appears. That means the backend server has started and is fully functional.
								You can use one of the following user account to login and test the application :	<br/>	
								<br/>
								<table class="table table-sm">
									<tr>
										<th>Username</th>
										<th>Password</th>
										<th>Role</th>	
									</tr>
									<tr>
										<td>youneskasri@gmail.com</td>
										<td>mdp</td>
										<td>ADMIN</td>
									</tr>
									<tr>
										<td>tounes@gmail.com</td>
										<td>mdp</td>
										<td>ARCHITECT</td>
									</tr>
									<tr>
										<td>lounes@gmail.com</td>
										<td>mdp</td>
										<td>TESTER</td>
									</tr>																		
								</table>				
							</div>
						</div>
					</div>
					<div className="row mt-3">
					{
						this.state.backendHasStarted ?
							<Link to="/projects" className="btn btn-info mx-auto mt-5 btn-lg">Start now</Link>
							: ''
					}
					</div>
				</div>
			);
		}	
}


