import Axios from "axios";
const BASE_URL = require("../params").serverBaseUrl.replace('/api','');

let obj = {

	isAdmin: () => {
		return obj.is("ADMIN");
	},

	isManager: () => {
		return obj.is("MANAGER");
	},

	isTester: () => {
		return obj.is("TESTER");
	},

	is: (aRole) => {
		let role = sessionStorage.getItem("role");
		return (obj.isLogged() && role && role === aRole);
	},

	isLogged: () => {
		let token = sessionStorage.getItem("token");
		return (token && true);
	},

	logout: () => {
		sessionStorage.clear("token");
		sessionStorage.clear("firstName");
		sessionStorage.clear("role");
	},

	setItems: (result) => {
		let token_role_firstName = result.split(',');
		let token = token_role_firstName[0];
		let role = token_role_firstName[1];
		let firstName = token_role_firstName[2];

		sessionStorage.setItem("role", role);
		sessionStorage.setItem("firstName", firstName);
		sessionStorage.setItem("token", token);
	},

	login: (loginForm) => {
		return Axios.post(`${BASE_URL}/token`, loginForm)
		.then(res => res.data)
		.then(data => {
			if (data.includes("Incorrect"))
				throw new Error(data);

			return data;
		});
	}
}

export default obj;


